@font-face{
  font-family: 'Sinhala Sangam MN';
  src : url('./fonts/Sinhala\ Sangam\ MN.ttc');
}

@font-face{
  font-family: 'Optima';
  src : url('./fonts/Optima.ttc');
}

a {
  color: black;
  text-decoration:none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapperLayer__z_PKn {
  position: relative;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@media (max-width: 400px) {
  .sider {
    display: none;
  }
  .main {
    position: absolute;
    left: 10px;
  } 
}

@media (min-width: 400px) {
  .sider {
    position: fixed;
    top: 0;
    padding-top: 40px;
    bottom: 0;
    left: 0;
    width: 300px;
    border-right: 1px solid #e4e6e9;
    background-color: #fff;
    overflow: auto;
  }

  .sider-head {
    /* font-family: 'Sinhala Sangam MN'; */
    font-weight: bold;
    font-size: 37pt;
    text-align: right;
    margin-right: 10px;
  }

  .sider-head-xu{
    font-size: 34pt;
  }

  .nav {
    font-family: 'Optima';
    text-align: right;
    margin-right: 10px;
  }
  
  .nav-ul {
    list-style-type: none;
    text-align: right;
    line-height: 40px;
  }

  .nav-project {
    font-size: 11pt;
    font-weight: bold;
    margin-right: 10px;
  }

  .nav-contact {
    margin-top: 50px;
  }

  .nav-font {
    font-size: 15pt;
    margin-bottom: 10px;
  }

  .nav-font-bold {
    font-size: 15pt;
    font-weight: bold;
  }

  .main {
    position: absolute;
    padding-top: 40px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 321px;
    overflow-y: auto;
    background-color: #fff;
  }
}